import React from 'react';
import WizardHeader from './wizard/WizardHeader';
import WizardFooter from './wizard/WizardFooter';
import { Row, Col, FormGroup, ControlLabel, FormControl } from 'react-bootstrap';

class Upholstery extends React.Component {

	constructor() {
        super();
        this.state = {
            swatches : [],
            design : [],
            tuftedDesign : [],
            twoTonedFabric : ''            
        }
    }

    componentWillMount() {
        let { swatches, design, tuftedDesign, twoTonedFabric } = this.props.data;
        this.setState({ swatches, design, tuftedDesign, twoTonedFabric });
    }

    onSubmit = (e) => {
        // e.preventDefault();
        let { swatches, design, tuftedDesign, twoTonedFabric } = this.state;
        let upholstery = { swatches, design, tuftedDesign, twoTonedFabric };
        this.props.setData({ upholstery : upholstery });
        this.props.handleNext();
    }

    onChange = (e) => {
		this.setState({ [e.target.name]: e.target.value });
    }

    handleSwatchChange = (e) => {
        let newSwatches = this.state.swatches;
        newSwatches[parseInt(e.target.name, 10)]["status"] = e.target.checked;
        this.setState({ swatches : newSwatches });
    }

    handleDesignChange = (e) => {
        let newDesigns = this.state.design;
        newDesigns[parseInt(e.target.name, 10)]["status"] = e.target.checked;
        this.setState({ design : newDesigns });
    }

    handleTuftedDesignChange = (e) => {
        let newTuftedDesigns = this.state.tuftedDesign;
        newTuftedDesigns[parseInt(e.target.name, 10)]["status"] = e.target.checked;
        this.setState({ tuftedDesign : newTuftedDesigns });
    }
    
    renderUpholsterySwatches = (swatches) => {
        return swatches.map((data) => {
            return (
                <Col md={3} sm={6} xs={12} key={"swatches" + data.id}>
                    <div className="card">
                        <div className="card--image" style={{ 'backgroundImage' : 'url("' + process.env.PUBLIC_URL + data.imgSrc + '")' }}>
                            &nbsp;
                        </div>
                        <div className="card--desc">
                            <input type="checkbox" id={"swatches" + data.id} name={data.id} value="" checked={data.status} onChange={this.handleSwatchChange}/>
                            <label className="for-checkbox" htmlFor={"swatches" + data.id}>
                                <span></span>{data.name}
                            </label>
                        </div>
                    </div>
                </Col>
            );
        });
    }
    
    renderUpholsteryDesign = (designs) => {
        return designs.map((data) => {
            return (
                <Col md={3} sm={6} xs={12} key={"design" + data.id}>
                    <div className="card">
                        <div className="card--image" style={{ 'backgroundImage' : 'url("' + process.env.PUBLIC_URL + data.imgSrc + '")' }}>
                            &nbsp;
                        </div>
                        <div className="card--desc">
                            <input type="checkbox" id={"design" + data.id} name={data.id} value="" checked={data.status} onChange={this.handleDesignChange}/>
                            <label className="for-checkbox" htmlFor={"design" + data.id}>
                                <span></span>{data.name}
                            </label>
                        </div>
                    </div>
                </Col>
            );
        });
    }

    renderUpholsteryTuftedDesign = (tuftedDesigns) => {
        return tuftedDesigns.map((data) => {
            return (
                <Col md={3} sm={6} xs={12} key={"tuftedDesign" + data.id}>
                    <div className="card">
                        <div className="card--image" style={{ 'backgroundImage' : 'url("' + process.env.PUBLIC_URL + data.imgSrc + '")' }}>
                            &nbsp;
                        </div>
                        <div className="card--desc">
                            <input type="checkbox" id={"tuftedDesign" + data.id} name={data.id} value="" checked={data.status} onChange={this.handleTuftedDesignChange}/>
                            <label className="for-checkbox" htmlFor={"tuftedDesign" + data.id}>
                                <span></span>{data.name}
                            </label>
                        </div>
                    </div>
                </Col>
            );
        });
    }

	render() {
        const title = "Upholstery";
		return (
            <div>
                <WizardHeader title={title} />
                <div className="wizard--body">
                    <div className="container">
                        <div className="wizard--body-container">
                            <FormGroup controlId="formEmail" className="checkbox-group">
                                <ControlLabel>Select upholstery swatches</ControlLabel>
                                <Row>
                                    {this.renderUpholsterySwatches(this.state.swatches)}
                                </Row>
                            </FormGroup>
                        </div>
                        <div className="wizard--body-container">
                            <FormGroup controlId="formEmail" className="checkbox-group">
                                <ControlLabel>Select upholstery design</ControlLabel>
                                <Row>
                                    {this.renderUpholsteryDesign(this.state.design)}
                                </Row>
                            </FormGroup>
                            <FormGroup controlId="formEmail" className="checkbox-group">
                                <ControlLabel>(Optional tufted design)</ControlLabel>
                                <Row>
                                    {this.renderUpholsteryTuftedDesign(this.state.tuftedDesign)}  
                                </Row>
                            </FormGroup>
                            <Row>
                                <Col md={6}>
                                    <FormGroup controlId="formtwoTonedFabric">
                                        <ControlLabel>Please specify if two-toned toned fabric</ControlLabel>
                                        <FormControl
                                            componentClass="textarea"
                                            placeholder="Enter comment(s) here"
                                            name="twoTonedFabric"
                                            value={this.state.twoTonedFabric}
                                            onChange={this.onChange}
                                        />
                                    </FormGroup>
                                </Col>
                            </Row>
                        </div>
                    </div>
                </div>
                <WizardFooter 
                    handleNext={this.onSubmit} 
                    handlePrev={this.props.handlePrev} 
                />
			</div>
		)
	}
}

export default Upholstery


