import React from 'react';
import WizardHeader from './wizard/WizardHeader';
import WizardFooter from './wizard/WizardFooter';
import { Row, Col, FormGroup, ControlLabel, FormControl } from 'react-bootstrap';

class Dimensions extends React.Component {

	constructor() {
        super();
        this.state = {
            length : '',
            depth : '',
            height : '',
            floorToMattressHeight : '',
            seatHeight : '',
            sizes : []
        }
    }

    componentWillMount() {
        let { length, depth, height, floorToMattressHeight, seatHeight, sizes } = this.props.data;
        this.setState({ length, depth, height, floorToMattressHeight, seatHeight, sizes });
    }

    onChange = (e) => {
		this.setState({ [e.target.name]: e.target.value });
	}

    onSubmit = (e) => {
        // e.preventDefault();
        let { length, depth, height, floorToMattressHeight, seatHeight, sizes } = this.state;
        let dimensions = { length, depth, height, floorToMattressHeight, seatHeight, sizes };
        this.props.setData({dimensions : dimensions});
        this.props.handleNext();
    }
    
    handleSizeChange = (e) => {
        let newSizes = this.state.sizes;
        newSizes[parseInt(e.target.name, 10)]["status"] = e.target.checked;
        this.setState({ sizes : newSizes });
    }

    renderSizes = (sizes) => {
        return sizes.map((data) => {
            return (
                <div className="checkbox-group-item" key={"sizes" + data.id}>
                    <input type="checkbox" id={"sizes" + data.id} name={data.id} value="" checked={data.status} onChange={this.handleSizeChange} />
                    <label className="for-checkbox" htmlFor={"sizes" + data.id}>
                        <span></span>{data.name}
                    </label>
                </div>
            );
        });
    }

	render() {
        const title = "Dimensions";
		return (
            <div>
                <WizardHeader title={title} />
                <div className="wizard--body">
                    <div className="container">
                        <div className="wizard--body-container">
                            <Row>
                                <Col lg={6}>
                                    <FormGroup controlId="formEmail">
                                        <ControlLabel>Length</ControlLabel>
                                        <FormControl
                                            type="text"
                                            placeholder="Enter length in inches"
                                            name="length"
                                            value={this.state.length}
                                            onChange={this.onChange}
                                        />
                                    </FormGroup>
                                    <FormGroup controlId="formEmail">
                                        <ControlLabel>Depth</ControlLabel>
                                        <FormControl
                                            type="text"
                                            placeholder="Enter depth in inches"
                                            name="depth"
                                            value={this.state.depth}
                                            onChange={this.onChange}
                                        />
                                    </FormGroup>
                                    <FormGroup controlId="formEmail">
                                        <ControlLabel>Height</ControlLabel>
                                        <FormControl
                                            type="text"
                                            placeholder="Enter height in inches"
                                            name="height"
                                            value={this.state.height}
                                            onChange={this.onChange}
                                        />
                                    </FormGroup>
                                </Col>
                                <Col lg={6}>
                                    <FormGroup controlId="formEmail">
                                        <ControlLabel>Seat Height (for sofas/chairs only)</ControlLabel>
                                        <FormControl
                                            type="text"
                                            name="seatHeight"
                                            placeholder="Enter height in inches"
                                            value={this.state.seatHeight}
                                            onChange={this.onChange}
                                        />
                                    </FormGroup>
                                </Col>
                            </Row>
                        </div>
                        <div className="wizard--body-container">
                            <FormGroup className="checkboxes">
                                <ControlLabel>For Beds and Mattresses</ControlLabel>
                            </FormGroup>
                            <Row>
                                <Col sm={6} xs={12}>
                                    <FormGroup controlId="formEmail">
                                        <ControlLabel>Floor to Mattress Height</ControlLabel>
                                        <FormControl
                                            type="text"
                                            placeholder="Enter height in inches"
                                            name="floorToMattressHeight"
                                            value={this.state.floorToMattressHeight}
                                            onChange={this.onChange}
                                        />
                                    </FormGroup>
                                    <FormGroup className="checkboxes">
                                        <ControlLabel className="margin-bottom-20">Sizes</ControlLabel>
                                        {this.renderSizes(this.state.sizes)}
                                    </FormGroup>
                                </Col>
                            </Row>
                        </div>
                    </div>
                </div>
                <WizardFooter handleNext={this.onSubmit} prev={false} />
			</div>
		)
	}
}

export default Dimensions


