import React from 'react';
import WizardHeader from './wizard/WizardHeader';
import WizardFooter from './wizard/WizardFooter';
import { Row, Col, FormGroup, ControlLabel } from 'react-bootstrap';

class Accessories extends React.Component {

	constructor() {
        super();
        this.state = {
            crystal : [],
            studs : []
        }
    }

    componentWillMount() {
        let { crystal, studs } = this.props.data;
        this.setState({ crystal, studs });
    }

    onSubmit = (e) => {
        // e.preventDefault();
        let { crystal, studs } = this.state;
        let accessories = { crystal, studs };
        this.props.setData({ accessories : accessories });
        this.props.handleNext();
    }

    handleCrystalChange = (e) => {
        let newCrystal = this.state.crystal;
        newCrystal[parseInt(e.target.name, 10)]["status"] = e.target.checked;
        this.setState({ crystal : newCrystal });
    }

    handleStudsChange = (e) => {
        let newStuds = this.state.studs;
        newStuds[parseInt(e.target.name, 10)]["status"] = e.target.checked;
        this.setState({ studs : newStuds });
    }
    
    renderCrystals = (crystal) => {
        return crystal.map((data) => {
            return (
                <Col md={3} sm={6} xs={12} key={"crystal" + data.id}>
                    <div className="card">
                        <div className="card--image" style={{ 'backgroundImage' : 'url("' + process.env.PUBLIC_URL + data.imgSrc + '")' }}>
                            &nbsp;
                        </div>
                        <div className="card--desc">
                            <input type="checkbox" id={"crystal" + data.id} name={data.id} value="" checked={data.status} onChange={this.handleCrystalChange}/>
                            <label className="for-checkbox" htmlFor={"crystal" + data.id}>
                                <span></span>{data.name}
                            </label>
                        </div>
                    </div>
                </Col>
            );
        });
    }

    renderStuds = (studs) => {
        return studs.map((data) => {
            return (
                <Col md={3} sm={6} xs={12} key={"studs" + data.id}>
                    <div className="card">
                        <div className="card--image" style={{ 'backgroundImage' : 'url("' + process.env.PUBLIC_URL + data.imgSrc + '")' }}>
                            &nbsp;
                        </div>
                        <div className="card--desc">
                            <input type="checkbox" id={"studs" + data.id} name={data.id} value="" checked={data.status} onChange={this.handleStudsChange}/>
                            <label className="for-checkbox" htmlFor={"studs" + data.id}>
                                <span></span>{data.name}
                            </label>
                        </div>
                    </div>
                </Col>
            );
        });
    }

	render() {
        const title = "Accessories";
		return (
            <div>
                <WizardHeader title={title} />
                <div className="wizard--body">
                    <div className="container">
                        <div className="wizard--body-container">
                            <FormGroup controlId="formEmail" className="checkbox-group">
                                <ControlLabel>Select crystal</ControlLabel>
                                <Row>
                                    {this.renderCrystals(this.state.crystal)}
                                </Row>
                            </FormGroup>
                            <FormGroup controlId="formEmail">
                                <ControlLabel>Select studs</ControlLabel>
                                <Row>
                                {this.renderStuds(this.state.studs)}
                                </Row>
                            </FormGroup>
                        </div>
                    </div>
                </div>
                <WizardFooter 
                    handleNext={this.onSubmit} 
                    handlePrev={this.props.handlePrev} />
			</div>
		)
	}
}

export default Accessories


