import React from 'react';

class WizardFooter extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            prev : true,
            next : true
        }
    }

    componentWillMount() {
        let { prev, next } = this.props;
        if(prev === undefined)
            prev = true;
        if(next === undefined)
            next = true;
        this.setState({ prev, next });
    }

    renderPrev = () => {
        return(
            <div className="prev">
                <a href="#" className="btn-eb solid dark" onClick={this.props.handlePrev}>
                    <i className="fa fa-arrow-left" aria-hidden="true"></i> <text className="hidden-xs">Prev</text>
                </a>
            </div>
        );
    }

    renderNext = () => {
        return(
            <div className="next">
                <a href="#" className="btn-eb solid dominant" onClick={this.props.handleNext}>
                    <text className="hidden-xs">Next</text> <i className="fa fa-arrow-right" aria-hidden="true"></i>
                </a>
            </div>
        );
    }

	render() {
		return (
            <div className="wizard--footer">
                <div className="container">
                    {this.state.prev && this.renderPrev()}
                    {this.state.next && this.renderNext()}
                </div>
            </div>
		)
	}
}

export default WizardFooter


