import React from 'react';
import WizardHeader from './wizard/WizardHeader';
import WizardFooter from './wizard/WizardFooter';
import { Row, Col, FormGroup, ControlLabel } from 'react-bootstrap';

class Finish extends React.Component {

	constructor() {
        super();
        this.state = {
            finish : []
        }
    }

    componentWillMount() {
        let { finish } = this.props.data;
        this.setState({ finish });
    }

    onSubmit = (e) => {
        // e.preventDefault();
        let { finish } = this.state;
        let finishes = { finish };
        this.props.setData({ finish : finishes });
        this.props.handleNext();
    }

    handleFinishChange = (e) => {
        let newFinish = this.state.finish;
        newFinish[parseInt(e.target.name, 10)]["status"] = e.target.checked;
        this.setState({ finish : newFinish });
    }

    renderFinishes = (finish) => {
        return finish.map((data) => {
            return (
                <Col md={3} sm={6} xs={12} key={"finish" + data.id}>
                    <div className="card">
                        <div className="card--image" style={{ 'backgroundImage' : 'url("' + process.env.PUBLIC_URL + data.imgSrc + '")' }}>
                            &nbsp;
                        </div>
                        <div className="card--desc">
                            <input type="checkbox" id={"finish" + data.id} name={data.id} value="" checked={data.status} onChange={this.handleFinishChange}/>
                            <label className="for-checkbox" htmlFor={"finish" + data.id}>
                                <span></span>{data.name}
                            </label>
                        </div>
                    </div>
                </Col>
            );
        });
    }

	render() {
        const title = "Finish";
		return (
            <div>
                <WizardHeader title={title} />
                <div className="wizard--body">
                    <div className="container">
                        <div className="wizard--body-container">
                            <FormGroup controlId="formEmail" className="checkbox-group">
                                <ControlLabel>Select finish</ControlLabel>
                                <Row>
                                    {this.renderFinishes(this.state.finish)}
                                </Row>
                            </FormGroup>
                        </div>
                    </div>
                </div>
                <WizardFooter 
                    handleNext={this.onSubmit} 
                    handlePrev={this.props.handlePrev} />
			</div>
		)
	}
}

export default Finish


