import React, { Component } from 'react';
import Dimensions from './components/Dimensions';
import Upholstery from './components/Upholstery';
import Accessories from './components/Accessories';
import Finish from './components/Finish';
import Materials from './components/Materials';
import Summary from './components/Summary';
import PlaceOrder from './components/PlaceOrder';
import ProgressBar from './components/ProgressBar';

class App extends Component {

  constructor() {
		super();
		this.state = {
      step : 1,
      dimensions : {
        length : '',
        depth : '',
        height : '',
        floorToMattressHeight : '',
        seatHeight : '',
        sizes : [
          {
            id : 0,
            name : 'Single 36 x 75',
            status : false
          },
          {
            id : 1,
            name : 'Double 48 x 75',
            status : false
          },
          {
            id : 2,
            name : 'Twin 54 x 75',
            status : false
          },
          {
            id : 3,
            name : 'Queen 60 x 75',
            status : false
          },
          {
            id : 4,
            name : 'King 72 x 78',
            status : false
          }
        ]
      },
      upholstery : {
        swatches : [
          {
            id : 0,
            name : 'Name',
            imgSrc : '/images/sample.jpg',
            status : false
          },
          {
            id : 1,
            name : 'Name',
            imgSrc : '/images/sample.jpg',
            status : false
          },
          {
            id : 2,
            name : 'Name',
            imgSrc : '/images/sample.jpg',
            status : false
          }
        ],
        design : [
          {
            id : 0,
            name : 'Plain',
            imgSrc : '/images/sample.jpg',
            status : false
          },
          {
            id : 1,
            name : 'Square tufted',
            imgSrc : '/images/sample.jpg',
            status : false
          },
          {
            id : 2,
            name : 'Diamond tufted',
            imgSrc : '/images/sample.jpg',
            status : false
          }
        ],
        tuftedDesign : [
          {
            id : 0,
            name : 'Stiched',
            imgSrc : '/images/sample.jpg',
            status : false
          },
          {
            id : 1,
            name : 'Quilted',
            imgSrc : '/images/sample.jpg',
            status : false
          }
        ],
        twoTonedFabric : ''
      },
      accessories : {
        crystal : [
          {
            id : 0,
            name : 'Clear',
            imgSrc : '/images/sample.jpg',
            status : false
          },
          {
            id : 1,
            name : 'Gray',
            imgSrc : '/images/sample.jpg',
            status : false
          },
          {
            id : 2,
            name : 'Multi-colored',
            imgSrc : '/images/sample.jpg',
            status : false
          }
        ],
        studs : [
          {
            id : 0,
            name : 'Silver',
            imgSrc : '/images/sample.jpg',
            status : false
          },
          {
            id : 1,
            name : 'Gold',
            imgSrc : '/images/sample.jpg',
            status : false
          },
          {
            id : 2,
            name : 'Bronze',
            imgSrc : '/images/sample.jpg',
            status : false
          },
          {
            id : 3,
            name : 'Antique gold',
            imgSrc : '/images/sample.jpg',
            status : false
          }
        ]
      },
      finish : {
        finish : [
          {
            id : 0,
            name : 'Option',
            imgSrc : '/images/sample.jpg',
            status : false
          },
          {
            id : 1,
            name : 'Option',
            imgSrc : '/images/sample.jpg',
            status : false
          },
          {
            id : 2,
            name : 'Option',
            imgSrc : '/images/sample.jpg',
            status : false
          },
          {
            id : 3,
            name : 'Option',
            imgSrc : '/images/sample.jpg',
            status : false
          }
        ]
      },
      materials : {
        material : [
          {
            id : 0,
            name : 'Wood',
            imgSrc : '/images/sample.jpg',
            status : false
          },
          {
            id : 1,
            name : 'Metal',
            imgSrc : '/images/sample.jpg',
            status : false
          },
          {
            id : 2,
            name : 'Stainless',
            imgSrc : '/images/sample.jpg',
            status : false
          },
          {
            id : 3,
            name : 'Glass',
            imgSrc : '/images/sample.jpg',
            status : false
          },
          {
            id : 4,
            name : 'Mirror',
            imgSrc : '/images/sample.jpg',
            status : false
          }
        ],
        glassFinish : [
          {
            id : 0,
            name : 'Flat Polished Edge',
            imgSrc : '/images/sample.jpg',
            status : false
          },
          {
            id : 1,
            name : 'Rounded Corners',
            imgSrc : '/images/sample.jpg',
            status : false
          },
          {
            id : 2,
            name : 'Bevelled Edge',
            imgSrc : '/images/sample.jpg',
            status : false
          }
        ],
        glassColor : '',
        brand : '',
        kindOfMattress : [
          {
            id : 0,
            name : 'Solid Foam',
            status : false
          },
          {
            id : 1,
            name : 'Spring Mattress',
            status : false
          }
        ],
        typeOfGlass : [
          {
            id : 0,
            name : 'Regular glass',
            status : false
          },
          {
            id : 1,
            name : 'Tempered',
            status : false
          }
        ]
      }
    }
  }

  handleNext = () => {
		let { step } = this.state;
		if(step >= 7)
			return;
		step++;
		this.setState({ step });
	}

	handlePrev = () => {
		let { step } = this.state;
		if(step <= 1)
			return;
		step--;
		this.setState({ step });
	}

	setData = (newState) => {
		if(!newState)
			return;
		this.setState(newState);
	}
  
  render() {

    let { step } = this.state;
    return (
      <div>
        <div className="progressbar-container">
          <ProgressBar step={step}/>
        </div>
        <div className="wizard">
          {step === 1 && 
            <Dimensions
              data={this.state.dimensions}
              setData={this.setData}
              handleNext={this.handleNext}
              handlePrev={this.handlePrev}
            />
          }
          {step === 2 && 
            <Upholstery
              data={this.state.upholstery}
              setData={this.setData}
              handleNext={this.handleNext}
              handlePrev={this.handlePrev}
            />
          }
          {step === 3 && 
            <Accessories
              data={this.state.accessories}
              setData={this.setData}
              handleNext={this.handleNext}
              handlePrev={this.handlePrev}
            />
          }
          {step === 4 && 
            <Finish
              data={this.state.finish}
              setData={this.setData}
              handleNext={this.handleNext}
              handlePrev={this.handlePrev}
            />
          }
          {step === 5 && 
            <Materials
              data={this.state.materials}
              setData={this.setData}
              handleNext={this.handleNext}
              handlePrev={this.handlePrev}
            />
          }
          {step === 6 && 
            <Summary
              data={this.state}
              setData={this.setData}
              handleNext={this.handleNext}
              handlePrev={this.handlePrev}
            />
          }
          {step === 7 && 
            <PlaceOrder
              data={this.state}
              setData={this.setData}
              handleNext={this.handleNext}
              handlePrev={this.handlePrev}
            />
          }
        </div>
      </div>
    );
  }
}

export default App;



