import React from 'react';
import WizardHeader from './wizard/WizardHeader';
import WizardFooter from './wizard/WizardFooter';
import { Row, Col, FormGroup, ControlLabel, FormControl } from 'react-bootstrap';

class Materials extends React.Component {

	constructor() {
        super();
        this.state = {
            material : [],
            glassFinish : [],
            glassColor : '',
            brand : '',
            kindOfMattress : [],
            typeOfGlass : []
        }
    }

    componentWillMount() {
        let { material, glassFinish, glassColor, brand, kindOfMattress, typeOfGlass } = this.props.data;
        this.setState({ material, glassFinish, glassColor, brand, kindOfMattress, typeOfGlass });
    }

    onSubmit = (e) => {
        // e.preventDefault();
        let { material, glassFinish, glassColor, brand, kindOfMattress, typeOfGlass } = this.state;
        let materials = { material, glassFinish, glassColor, brand, kindOfMattress, typeOfGlass };
        this.props.setData({ materials : materials });
        this.props.handleNext();
    }

    onChange = (e) => {
		this.setState({ [e.target.name]: e.target.value });
    }

    handleMaterialChange = (e) => {
        let newMaterial = this.state.material;
        newMaterial[parseInt(e.target.name, 10)]["status"] = e.target.checked;
        this.setState({ material : newMaterial });
    }

    handleGlassFinishChange = (e) => {
        let newGlassFinish = this.state.glassFinish;
        newGlassFinish[parseInt(e.target.name, 10)]["status"] = e.target.checked;
        this.setState({ glassFinish : newGlassFinish });
    }

    handleMattressChange = (e) => {
        let newMattress = this.state.kindOfMattress;
        newMattress[parseInt(e.target.name, 10)]["status"] = e.target.checked;
        this.setState({ kindOfMattress : newMattress });
    }

    handleGlassChange = (e) => {
        let newGlass = this.state.typeOfGlass;
        newGlass[parseInt(e.target.name, 10)]["status"] = e.target.checked;
        this.setState({ typeOfGlass : newGlass });
    }

    renderMaterials = (material) => {
        return material.map((data) => {
            return (
                <Col md={3} sm={6} xs={12} key={"material" + data.id}>
                    <div className="card">
                        <div className="card--image" style={{ 'backgroundImage' : 'url("' + process.env.PUBLIC_URL + data.imgSrc + '")' }}>
                            &nbsp;
                        </div>
                        <div className="card--desc">
                            <input type="checkbox" id={"material" + data.id} name={data.id} value="" checked={data.status} onChange={this.handleMaterialChange}/>
                            <label className="for-checkbox" htmlFor={"material" + data.id}>
                                <span></span>{data.name}
                            </label>
                        </div>
                    </div>
                </Col>
            );
        });
    }

    renderMattress = (kindOfMattress) => {
        return kindOfMattress.map((data) => {
            return (
                <div className="checkbox-group-item" key={"mattress" + data.id}>
                    <input type="checkbox" id={"mattress" + data.id} name={data.id} value="" checked={data.status} onChange={this.handleMattressChange} />
                    <label className="for-checkbox" htmlFor={"mattress" + data.id}>
                        <span></span>{data.name}
                    </label>
                </div>
            );
        });
    }

    renderGlass = (typeOfGlass) => {
        return typeOfGlass.map((data) => {
            return (
                <div className="checkbox-group-item" key={"glass" + data.id}>
                    <input type="checkbox" id={"glass" + data.id} name={data.id} value="" checked={data.status} onChange={this.handleGlassChange} />
                    <label className="for-checkbox" htmlFor={"glass" + data.id}>
                        <span></span>{data.name}
                    </label>
                </div>
            );
        });
    }

    renderGlassFinish = (glassFinish) => {
        return glassFinish.map((data) => {
            return (
                <Col md={3} sm={6} xs={12} key={"glassFinish" + data.id}>
                    <div className="card">
                        <div className="card--image" style={{ 'backgroundImage' : 'url("' + process.env.PUBLIC_URL + data.imgSrc + '")' }}>
                            &nbsp;
                        </div>
                        <div className="card--desc">
                            <input type="checkbox" id={"glassFinish" + data.id} name={data.id} value="" checked={data.status} onChange={this.handleGlassFinishChange}/>
                            <label className="for-checkbox" htmlFor={"glassFinish" + data.id}>
                                <span></span>{data.name}
                            </label>
                        </div>
                    </div>
                </Col>
            );
        });
    }

	render() {
        const title = "Materials";
		return (
            <div>
                <WizardHeader title={title} />
                <div className="wizard--body">
                    <div className="container">
                        <div className="wizard--body-container">
                            <FormGroup controlId="formEmail" className="checkbox-group">
                                <ControlLabel>Select material</ControlLabel>
                                <Row>
                                    {this.renderMaterials(this.state.material)}
                                </Row>
                            </FormGroup>
                        </div>
                        <div className="wizard--body-container">
                            <FormGroup className="checkboxes">
                                <ControlLabel>For Beds and Mattresses</ControlLabel>
                            </FormGroup>
                            <Row className="margin-bottom-20">
                                <Col sm={6} xs={12}>
                                    <FormGroup className="checkboxes">
                                        <ControlLabel className="margin-bottom-20">Kind of mattress</ControlLabel>
                                        {this.renderMattress(this.state.kindOfMattress)}
                                    </FormGroup>
                                </Col>
                            </Row>
                            <Row>
                                <Col md={6} sm={12}>
                                    <FormGroup controlId="formEmail">
                                        <ControlLabel>Please specify brand and specifications</ControlLabel>
                                        <FormControl
                                            componentClass="textarea"
                                            placeholder="Enter brand and specifications"
                                            name="brand"
                                            value={this.state.brand}
                                            onChange={this.onChange}
                                        />
                                    </FormGroup>
                                </Col>
                            </Row>
                        </div>
                        <div className="wizard--body-container">
                            <FormGroup className="checkboxes">
                                <ControlLabel>For Glass</ControlLabel>
                            </FormGroup>
                            <Row>
                                <Col md={3} sm={6} xs={12}>
                                    <FormGroup className="checkboxes">
                                        <ControlLabel className="margin-bottom-20">Type</ControlLabel>
                                        {this.renderGlass(this.state.typeOfGlass)}
                                    </FormGroup>
                                </Col>
                            </Row>
                            <Row className="margin-bottom-20">
                                <Col md={6} sm={12}>
                                    <FormGroup controlId="formEmail">
                                        <ControlLabel>Please specify color</ControlLabel>
                                        <FormControl
                                            type="text"
                                            placeholder="Enter color"
                                            name="glassColor"
                                            value={this.state.glassColor}
                                            onChange={this.onChange}
                                        />
                                    </FormGroup>
                                </Col>
                            </Row>
                            <FormGroup controlId="formEmail" className="checkbox-group">
                                <ControlLabel>Select Glass Finish</ControlLabel>
                                <Row>
                                    {this.renderGlassFinish(this.state.glassFinish)}
                                </Row>
                            </FormGroup>
                        </div>
                    </div>
                </div>
                <WizardFooter 
                    handleNext={this.onSubmit} 
                    handlePrev={this.props.handlePrev} />
			</div>
		)
	}
}

export default Materials


