import React from 'react';
import WizardHeader from './wizard/WizardHeader';
import WizardFooter from './wizard/WizardFooter';
import { FormGroup, ControlLabel, FormControl, HelpBlock } from 'react-bootstrap';

class PlaceOrder extends React.Component {

	constructor() {
        super();
        this.state = {
            name : '',
            contact : '',
            email : '',
            address : '',
            message : ''
        }
    }

    onChange = (e) => {
		this.setState({ [e.target.name]: e.target.value });
	}

    onSubmit = (e) => {
        // e.preventDefault();
        let { name, contact, email, address, message } = this.state;
        console.log(name, contact, email, address, message);
    }

	render() {
        const title = "Place Order";
		return (
            <div>
                <WizardHeader title={title} />
                <div className="wizard--body">
                    <div className="container">
                        <div className="wizard--body-container">
                            <FormGroup controlId="formName">
                                <ControlLabel>Your Name*</ControlLabel>
                                <FormControl
                                    type="text"
                                    placeholder="ex: Juan Dela Cruz"
                                    name="name"
                                    value={this.state.name}
                                    onChange={this.onChange}
                                />
                            </FormGroup>
                            <FormGroup controlId="formContact">
                                <ControlLabel>Your mobile or phone*</ControlLabel>
                                <FormControl
                                    type="text"
                                    placeholder="ex: 0917-559-9854 or (02) 831-1689"
                                    name="contact"
                                    value={this.state.contact}
                                    onChange={this.onChange}
                                />
                            </FormGroup>
                            <FormGroup controlId="formEmail">
                                <ControlLabel>Your email*</ControlLabel>
                                <FormControl
                                    type="text"
                                    placeholder="ex: juan_dela_cruz@samplemail.com"
                                    name="email"
                                    value={this.state.email}
                                    onChange={this.onChange}
                                />
                            </FormGroup>
                            <FormGroup controlId="formAddress">
                                <ControlLabel>Your complete address*</ControlLabel>
                                <FormControl
                                    componentClass="textarea"
                                    placeholder="House/Bldg #, Street, Village/Subdv, Barangay, City, Postal Code"
                                    name="address"
                                    value={this.state.address}
                                    onChange={this.onChange}
                                />
                            </FormGroup>
                        </div>
                        <div className="wizard--body-container">
                            <FormGroup controlId="formMessage">
                                <ControlLabel>Additional message (optional)</ControlLabel>
                                <FormControl
                                    componentClass="textarea"
                                    placeholder="Enter your message here"
                                    name="message"
                                    value={this.state.message}
                                    onChange={this.onChange}
                                />
                            </FormGroup>
                        </div>
                    </div>
                </div>
                <WizardFooter 
                    handleNext={this.onSubmit} 
                    handlePrev={this.props.handlePrev} />
			</div>
		)
	}
}

export default PlaceOrder


