import React, { Component } from 'react';

class ProgressBar extends Component {

	render() {
        const { step } = this.props;
        const activeClass = 'active';
        const doneClass = 'done';
		return (
            <div className="container for-progressbar">
                <div className="wizard-title">Custom Order</div>
                <ul className="progressbar hidden-xs">
                    <li className={step === 1 ? activeClass : '' || step > 1 ? doneClass : ''}>Dimensions</li>
                    <li className={step === 2 ? activeClass : '' || step > 2 ? doneClass : ''}>Upholstery</li>
                    <li className={step === 3 ? activeClass : '' || step > 3 ? doneClass : ''}>Accessories</li>
                    <li className={step === 4 ? activeClass : '' || step > 4 ? doneClass : ''}>Finish</li>
                    <li className={step === 5 ? activeClass : '' || step > 5 ? doneClass : ''}>Materials</li>
                    <li className={step === 6 ? activeClass : '' || step > 6 ? doneClass : ''}>Summary</li>
                    <li className={step === 7 ? activeClass : '' || step > 7 ? doneClass : ''}>Place Order</li>
                </ul>
                <div className="text-center visible-xs step-no">
                    Step {step} of 7
                </div>
			</div>
		);
	}
}

export default ProgressBar


