import React from 'react';
import WizardHeader from './wizard/WizardHeader';
import WizardFooter from './wizard/WizardFooter';
import { Row, Col, Table } from 'react-bootstrap';

class Summary extends React.Component {

    onSubmit = (e) => {
        // e.preventDefault();
        this.props.handleNext();
	}

	render() {
        const title = "Summary";
        let { dimensions, upholstery, accessories, finish, materials } = this.props.data,
            bedSizeCtr = 0,
            upholsteryDesignCtr = 0,
            tuftedDesignCtr = 0,
            swatchesCtr = 0,
            crystalCtr = 0,
            studsCtr = 0,
            finishCtr = 0,
            materialCtr = 0,
            mattressCtr = 0,
            glassTypeCtr = 0,
            glassFinishCtr = 0;
		return (
            <div className="summary">
                <WizardHeader title={title} />
                <div className="wizard--body">
                    <div className="container">
                        <div className="wizard--body-container">
                            <h3 className="margin-bottom-20">Dimensions</h3>
                            <hr />
                            <Row className="margin-bottom-20">
                                <Col md={12}>
                                    <Table striped condensed>
                                        <thead>
                                            <tr>
                                            <th>Label</th>
                                            <th>(inches)</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr>
                                                <td className="title">Length</td>
                                                <td className="value">{dimensions.length === '' ? 'n/a' : dimensions.length}</td>
                                            </tr>
                                            <tr>
                                                <td className="title">Depth</td>
                                                <td className="value">{dimensions.depth === '' ? 'n/a' : dimensions.depth}</td>
                                            </tr>
                                            <tr>
                                                <td className="title">Height</td>
                                                <td className="value">{dimensions.height === '' ? 'n/a' : dimensions.height}</td>
                                            </tr>
                                            <tr>
                                                <td className="title">Seat Height (for sofa/chair)</td>
                                                <td className="value">{dimensions.seatHeight === '' ? 'n/a' : dimensions.seatHeight}</td>
                                            </tr>
                                            <tr>
                                                <td className="title">Floor to Mattress Height (for bed)</td>
                                                <td className="value">{dimensions.floorToMattressHeight === '' ? 'n/a' : dimensions.floorToMattressHeight}</td>
                                            </tr>
                                        </tbody>
                                    </Table>
                                </Col>
                            </Row>
                            <Row>
                                <Col md={12}>
                                    <Table striped condensed>
                                        <thead>
                                            <tr>
                                            <th>Label</th>
                                            <th>Size</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {
                                                dimensions.sizes.map((data) => {
                                                    if(data.status) {
                                                        bedSizeCtr++;
                                                        return(
                                                            <tr>
                                                                <td className="title">Bed Size</td>
                                                                <td className="value">{data.name}</td>
                                                            </tr>
                                                        );
                                                    }
                                                    return;
                                                })
                                            }
                                            {
                                                bedSizeCtr === 0 && (
                                                    <tr>
                                                        <td className="value empty" colSpan={2}>No specified bed size</td>
                                                    </tr>
                                                )
                                            }
                                        </tbody>
                                    </Table>
                                </Col>
                            </Row>
                        </div>
                        <div className="wizard--body-container">
                            <h3>Uphosltery</h3>
                            <hr />
                            <div className="summary-section">
                                <div className="section-title"> Upholstery Swatches </div>
                                <Row>
                                {
                                    upholstery.swatches.map((data) => {
                                        if(data.status) {
                                            swatchesCtr++;
                                            return(
                                                <Col md={3} sm={6} xs={12} key={"swatches" + data.id}>
                                                    <div className="card">
                                                        <div className="card--image" style={{ 'backgroundImage' : 'url("' + process.env.PUBLIC_URL + data.imgSrc + '")' }}>
                                                            &nbsp;
                                                        </div>
                                                        <div className="card--desc">
                                                            {data.name}
                                                        </div>
                                                    </div>
                                                </Col>
                                            );
                                        }
                                    })
                                }
                                {
                                    swatchesCtr === 0 && (
                                        <Col md={12}>
                                            <div className="empty-state">No specified swatch</div>
                                        </Col>
                                    )
                                }
                                </Row>
                            </div>
                            <div className="summary-section">
                                <div className="section-title"> Upholstery Design </div>
                                <Row>
                                {
                                    upholstery.design.map((data) => {
                                        if(data.status) {
                                            upholsteryDesignCtr++;
                                            return(
                                                <Col md={3} sm={6} xs={12} key={"design" + data.id}>
                                                    <div className="card">
                                                        <div className="card--image" style={{ 'backgroundImage' : 'url("' + process.env.PUBLIC_URL + data.imgSrc + '")' }}>
                                                            &nbsp;
                                                        </div>
                                                        <div className="card--desc">
                                                            {data.name}
                                                        </div>
                                                    </div>
                                                </Col>
                                            );
                                        }
                                    })
                                }
                                {
                                    upholsteryDesignCtr === 0 && (
                                        <Col md={12}>
                                            <div className="empty-state">No specified upholstery design</div>
                                        </Col>
                                    )
                                }
                                </Row>
                            </div>
                            <div className="summary-section">
                                <div className="section-title"> Tufted Design </div>
                                <Row>
                                {
                                    upholstery.tuftedDesign.map((data) => {
                                        if(data.status) {
                                            tuftedDesignCtr++;
                                            return(
                                                <Col md={3} sm={6} xs={12} key={"tuftedDesign" + data.id}>
                                                    <div className="card">
                                                        <div className="card--image" style={{ 'backgroundImage' : 'url("' + process.env.PUBLIC_URL + data.imgSrc + '")' }}>
                                                            &nbsp;
                                                        </div>
                                                        <div className="card--desc">
                                                            {data.name}
                                                        </div>
                                                    </div>
                                                </Col>
                                            );
                                        }
                                    })
                                }
                                {
                                    tuftedDesignCtr === 0 && (
                                        <Col md={12}>
                                            <div className="empty-state">No specified tufted design</div>
                                        </Col>
                                    )
                                }
                                </Row>
                                {
                                    upholstery.twoTonedFabric !== '' && (
                                        <div>
                                            <div className="section-title"> Comment(s) for two-toned toned fabric </div>
                                            <Row>
                                                <Col md={12}>
                                                    <p>{upholstery.twoTonedFabric}</p>
                                                </Col>
                                            </Row>
                                        </div>
                                    )
                                }
                            </div>
                        </div>
                        <div className="wizard--body-container">
                            <h3>Accessories</h3>
                            <hr />
                            <div className="summary-section">
                                <div className="section-title"> Crystal </div>
                                <Row>
                                {
                                    accessories.crystal.map((data) => {
                                        if(data.status) {
                                            crystalCtr++;
                                            return(
                                                <Col md={3} sm={6} xs={12} key={"crsyal" + data.id}>
                                                    <div className="card">
                                                        <div className="card--image" style={{ 'backgroundImage' : 'url("' + process.env.PUBLIC_URL + data.imgSrc + '")' }}>
                                                            &nbsp;
                                                        </div>
                                                        <div className="card--desc">
                                                            {data.name}
                                                        </div>
                                                    </div>
                                                </Col>
                                            );
                                        }
                                    })
                                }
                                {
                                    crystalCtr === 0 && (
                                        <Col md={12}>
                                            <div className="empty-state">No specified crystal</div>
                                        </Col>
                                    )
                                }
                                </Row>
                            </div>
                            <div className="summary-section">
                                <div className="section-title"> Studs </div>
                                <Row>
                                {
                                    accessories.studs.map((data) => {
                                        if(data.status) {
                                            studsCtr++;
                                            return(
                                                <Col md={3} sm={6} xs={12} key={"studs" + data.id}>
                                                    <div className="card">
                                                        <div className="card--image" style={{ 'backgroundImage' : 'url("' + process.env.PUBLIC_URL + data.imgSrc + '")' }}>
                                                            &nbsp;
                                                        </div>
                                                        <div className="card--desc">
                                                            {data.name}
                                                        </div>
                                                    </div>
                                                </Col>
                                            );
                                        }
                                    })
                                }
                                {
                                    studsCtr === 0 && (
                                        <Col md={12}>
                                            <div className="empty-state">No specified studs</div>
                                        </Col>
                                    )
                                }
                                </Row>
                            </div>
                        </div>
                        <div className="wizard--body-container">
                            <h3>Finish</h3>
                            <hr />
                            <div className="summary-section">
                                <div className="section-title"> Finish </div>
                                <Row>
                                {
                                    finish.finish.map((data) => {
                                        if(data.status) {
                                            finishCtr++;
                                            return(
                                                <Col md={3} sm={6} xs={12} key={"finish" + data.id}>
                                                    <div className="card">
                                                        <div className="card--image" style={{ 'backgroundImage' : 'url("' + process.env.PUBLIC_URL + data.imgSrc + '")' }}>
                                                            &nbsp;
                                                        </div>
                                                        <div className="card--desc">
                                                            {data.name}
                                                        </div>
                                                    </div>
                                                </Col>
                                            );
                                        }
                                    })
                                }
                                {
                                    finishCtr === 0 && (
                                        <Col md={12}>
                                            <div className="empty-state">No specified finish</div>
                                        </Col>
                                    )
                                }
                                </Row>
                            </div>
                        </div>
                        <div className="wizard--body-container">
                            <h3>Materials</h3>
                            <hr />
                            <div className="summary-section">
                                <div className="section-title"> Material </div>
                                <Row>
                                {
                                    materials.material.map((data) => {
                                        if(data.status) {
                                            materialCtr++;
                                            return(
                                                <Col md={3} sm={6} xs={12} key={"material" + data.id}>
                                                    <div className="card">
                                                        <div className="card--image" style={{ 'backgroundImage' : 'url("' + process.env.PUBLIC_URL + data.imgSrc + '")' }}>
                                                            &nbsp;
                                                        </div>
                                                        <div className="card--desc">
                                                            {data.name}
                                                        </div>
                                                    </div>
                                                </Col>
                                            );
                                        }
                                    })
                                }
                                {
                                    materialCtr === 0 && (
                                        <Col md={12}>
                                            <div className="empty-state">No specified material</div>
                                        </Col>
                                    )
                                }
                                </Row>
                            </div>
                            <hr />
                            <div className="summary-section">
                                <div className="section-title"> For Beds and Mattresses </div>
                                <Row>
                                    <Col md={12}>
                                        <Table striped condensed>
                                            <thead>
                                                <tr>
                                                <th>Label</th>
                                                <th>Kind</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {
                                                    materials.kindOfMattress.map((data) => {
                                                        if(data.status) {
                                                            mattressCtr++;
                                                            return(
                                                                <tr>
                                                                    <td className="title">Mattress</td>
                                                                    <td className="value">{data.name}</td>
                                                                </tr>
                                                            );
                                                        }
                                                        return;
                                                    })
                                                }
                                                {
                                                    mattressCtr === 0 && (
                                                        <tr>
                                                            <td className="value empty" colSpan={2}>No specified kind of mattress</td>
                                                        </tr>
                                                    )
                                                }
                                            </tbody>
                                        </Table>
                                    </Col>
                                </Row>
                                {
                                    materials.brand !== '' && (
                                        <div>
                                            <div className="section-title"> Comment(s) for brand and specifications </div>
                                            <Row>
                                                <Col md={12}>
                                                    <p>{materials.brand}</p>
                                                </Col>
                                            </Row>
                                        </div>
                                    )
                                }
                            </div>
                            <hr />
                            <div className="summary-section">
                                <div className="section-title"> For Glass </div>
                                <Row>
                                    <Col md={12}>
                                        <Table striped condensed>
                                            <thead>
                                                <tr>
                                                <th>Label</th>
                                                <th>Type</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {
                                                    materials.typeOfGlass.map((data) => {
                                                        if(data.status) {
                                                            glassTypeCtr++;
                                                            return(
                                                                <tr>
                                                                    <td className="title">Glass</td>
                                                                    <td className="value">{data.name}</td>
                                                                </tr>
                                                            );
                                                        }
                                                        return;
                                                    })
                                                }
                                                {
                                                    glassTypeCtr === 0 && (
                                                        <tr>
                                                            <td className="value empty" colSpan={2}>No specified type of glass</td>
                                                        </tr>
                                                    )
                                                }
                                            </tbody>
                                        </Table>
                                    </Col>
                                </Row>
                                {
                                    materials.glassColor !== '' && (
                                        <div>
                                            <div className="section-title"> Color for glass </div>
                                            <Row>
                                                <Col md={12}>
                                                    <p>{materials.glassColor}</p>
                                                </Col>
                                            </Row>
                                        </div>
                                    )
                                }
                                <div className="summary-section">
                                    <div className="section-title"> Glass Finish </div>
                                    <Row>
                                    {
                                        materials.glassFinish.map((data) => {
                                            if(data.status) {
                                                glassFinishCtr++;
                                                return(
                                                    <Col md={3} sm={6} xs={12} key={"swatches" + data.id}>
                                                        <div className="card">
                                                            <div className="card--image" style={{ 'backgroundImage' : 'url("' + process.env.PUBLIC_URL + data.imgSrc + '")' }}>
                                                                &nbsp;
                                                            </div>
                                                            <div className="card--desc">
                                                                {data.name}
                                                            </div>
                                                        </div>
                                                    </Col>
                                                );
                                            }
                                        })
                                    }
                                    {
                                        glassFinishCtr === 0 && (
                                            <Col md={12}>
                                                <div className="empty-state">No specified glass finish</div>
                                            </Col>
                                        )
                                    }
                                    </Row>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <WizardFooter 
                    handleNext={this.onSubmit} 
                    handlePrev={this.props.handlePrev} />
			</div>
		)
	}
}

export default Summary


